.works
{
    padding: 0 3rem 0 3rem;
    display: flex;
    height: 90vh;
    margin-top: 8rem;
}

.worksRight
{
    position: relative;
}

.mainCircle
{
    left: 9rem;
    position: relative;
    width: 18rem;
    height: 18rem;
    border-radius: 100%;
    box-shadow: var(--smBoxShadow);
    top:2rem;
    background: white;
}

.secondCircle
{
    position: absolute;
}

.mainCircle>:nth-child(1)
{
    top:-3rem;
    left:6rem;
}
.mainCircle>:nth-child(2)
{
    left:-3rem;
    top:5rem;
}
.mainCircle>:nth-child(3)
{
    top:5rem;
    left:6rem;
}
.mainCircle>:nth-child(4)
{
    top:5rem;
    left:15rem;
}
.mainCircle>:nth-child(5)
{
    top:13rem;
    left:6rem;
}

.secondCircle
{
    width: 6rem;
    height: 6rem;
    position: absolute;
    border-radius: 100%;
    border: 4px solid #ebebeb;
    display: flex;
    align-items: center;
    justify-content: center;
    background: white;
    box-shadow: var(--smBoxShadow);
}

.secondCircle>img
{
    transform: scale(0.6);
}

.backCircle
{
    position: absolute;
    height: 13rem;
    width: 13rem;
    border-radius: 100%;
    /*background: black;*/
    z-index: -1;
}

.blue
{
    background: #1A89E1;
    left: 18rem;
    top:0rem;
}

.yellow
{
    background: #FDC807;
    left: 5rem;
    top: 12rem;
}